.skills {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    width: 100%;
} 
 
.skills-set {
    display: flex;
    justify-content: center;
    flex-direction: column;
    animation: flipInX 4s ease forwards;
}

.skills-sec {
    /* display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.skill {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    text-transform: uppercase;
    align-items: center;
}

@media only screen and (max-width: 400px) {
    
    .skills-set {
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 100px;
        align-items: center;
        margin: 0px auto;
    }
    
    .skills-sec {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .skill {
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 80%;
    }

    .parent {
        margin: 10px;
    }
}

@keyframes flipInX {
    0% {
        transform: perspective(400px) rotateY(90deg);
        opacity: 0;
    }
    40% {
        transform: perspective(400px) rotateY(-20deg);
    }
    60% {
        transform: perspective(400px) rotateY(10deg);
    }
    80% {
        transform: perspective(400px) rotateY(5deg);
    }
    100% {
        transform: perspective(400px);
        opacity: 1;
    }
}